import axios from 'axios'
import router from '../router/index'

const state ={
  token : localStorage.getItem('token')||'',
  user:{},
  posts:[],
  post:[],
  status:'',
  error:null
  
};
const getters ={
      isLoggedIn:state=>!!state.token,
      authState:state=>state.status,
      user:state=>state.user,
      posts:state=>state.posts,
      post:state=>state.post,
      error:state=>state.error,
};

const actions ={
    //login action
    async login({
        commit
    }, user){
        commit('auth_request');
        try{
            let res = await axios.post('/api/users/login', user);
        if(res.data.success){
            const token = res.data.token;
            const user = res.data.user;
            //store the token in our localstorage
            localStorage.setItem('token', token);
            //set axios default
            axios.defaults.headers.common['authorization'] = token;
            commit('auth_success', token, user);

        }
        return res;

        }catch(err){
            commit('auth_error', err)
                   
        }
    },
  //Register new user
  async register({
    commit
  }, userData){
    commit('register_request');
   try{
    let res = await axios.post('/api/users/register', userData);
    if(res.data.success !== undefined){
      commit('register_success');
    }
    return res;

   }catch(err){
    commit('register_error', err)
   }
  },
  //create new post
  async createPost({
    commit
  }, postData){
    commit('create_request');
    try{
      let res = await axios.post( `/api/posts/new`, postData);
      if(res.data.success !== undefined){
        commit('create_success');
      }
      return res;
    }catch(err){
      commit('create_error', err)
    }
  },
  //create new comment
  async createComment({
    commit
  }, postComment){
    commit('comment_request');
    try{
      let res = await axios.post(`/api/posts/comment`, postComment);
    if(res.data.success !== undefined){
      commit('comment_success');
    }
    return res;
    } catch(err){
      commit('comment_error', err)
    }
  },
  //create new Like

  async BlogLike({
    commit
  }, blogLike){
    commit('like_request');
    try{
      let res = await axios.post(`/api/posts/like`, blogLike);
      if(res.data.success !== undefined){
        commit('like_success');
      }
      return res;
    } catch(err){
      commit('like_error', err)
    }
  },

  //update post
  async updatePost({
    commit
  }, post){
    commit('update_request');
   try{
    let res = await axios.post('/api/posts/update', post);
    if(res.data.success !== undefined){
      commit('update_success');
    }
    return res;

   } catch(err){
    commit('update_error', err)
   }
  },
  //get profile
  async getProfile({commit}){
    let token = localStorage.getItem('token')
    commit('profile_request');
    try{
      let res = await axios.get('/api/users/profile',{
      headers:{
        'Authorization':'bearer '+ token
      }
    });
    commit('user_profile', res.data.user);
    return res;
  
    }catch(err){
      commit('profile_error', err)
     }
  },
  //logout route
  async logout({commit}){
    await localStorage.removeItem('token');
    commit('logout')
    delete axios.defaults.headers.common(['authorization']);
    router.push('/login')
    return
  },
  //Get all posts
 async getPosts({commit}){
    commit('post_request');
    try{
      let res = await axios.get("/api/posts/all");
      commit('user_post', res.data.data)
      return res
   
    } catch(err){
      commit('post_error', err)
     }
    },
   //Get single posts
 async singlePost({commit}){
  commit('single_request')
  try{
    let res = await axios.get("/api/posts/id");
   commit('single_success', res.data.data)
   return res
  } catch(err){
    commit('single_error', err)
   }
},
   //Get all posts
 async deletePost({commit}, post){
  commit('delete_request')
  try{
    let res = await axios.post("/api/posts/delete", post);
 if(res.data.success !== undefined){
  commit('delete_success');
}
   return res
  } catch(err){
    commit('delete_error', err)
   }
 
}
}


//mutations

const mutations ={
  auth_request(state){
    state.status = 'loading'
  },
  auth_success(state, token, user){
    state.token = token
    state.user = user
    state.status = 'success'
    state.error = null
  },
  auth_error(state, err){
    state.error = err.response.data.msg
  },

  register_request(state){
    state.status = 'loading'
  },
  register_success(state){
    state.error = null
    state.status = 'success'
  },
  register_error(state, err){
    state.error = err.response.data.msg
  },
 
  create_request(state){
    state.status = 'loading'
  },
  create_success(state){
    state.status = 'success'
    state.error = null
  },
  create_error(state, err){
    state.error = err.response.data.msg
  },
  comment_request(state){
    state.status = 'loading'
  },
  comment_success(state){
    state.status = 'success'
    state.error = null
  },
  comment_error(state, err){
    state.error = err.response.data.msg
  },
  like_request(state){
    state.status = 'loading'
  },
  like_success(state){
    state.status = 'success'
    state.error = null
  },
  like_error(state, err){
    state.error = err.response.data.msg
  },
  update_request(state){
    state.status = 'loading'
  },
  update_success(state){
    state.status = 'success' 
    state.error = null
  },
  update_error(state, err){
    state.error = err.response.data.msg
  },
  
  logout(state){
    state.error = null
    state.token = ''
    state.user = ''
    state.status = ''
  },
  profile_request(state){
    state.status = 'loading'
  },
  user_profile(state, user){
    state.user = user 
    state.error = null
  },
  profile_error(state, err){
    state.error = err.response.data.msg
  },
  post_request(state){
    state.status = 'loading'
  },
  user_post(state, post){
    state.posts = post
    state.error = null
  },
  post_error(state, err){
    state.error = err.response.data.msg
  },
  single_request(state){
    state.status = 'loading'
  },
  single_success(state, post){
    state.post = post
    state.error = null
  },
  single_error(state, err){
    state.error = err.response.data.msg
  },
  delete_request(state){
    state.status = 'loading'
  },
  delete_success(state){
    state.status = 'success'
    state.error = null
  },
  delete_error(state, err){
    state.error = err.response.data.msg
  },
}


export default{
    state,
    getters,
    actions,
    mutations
}

