import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import createStore from '../store/index'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import(/* webpackChunkName: "about" */ '../views/LoginView.vue'),
    meta:{
      requiresGuest:true
    } 
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import(/* webpackChunkName: "about" */ '../views/RegisterView.vue'),
    meta:{
      requiresGuest:true
    } 
    
  },
  {
    path: '/profile',
    name: 'Profile',
    component: () => import(/* webpackChunkName: "about" */ '../views/ProfileView.vue'),
    meta:{
      requiresAuth:true
    } 
  },
  {
    path: '/create',
    name: 'create post',
    component: () => import(/* webpackChunkName: "about" */ '../views/CreatePost.vue'),
    meta:{
      requiresAuth:true
    } 
    },
    {
      path: '/post/:id',
      name: 'single post',
      component: () => import(/* webpackChunkName: "about" */ '../views/singleView.vue'),
      meta:{
        requiresAuth:true
      } 
      },
      {
        path: '/update/:id',
        name: 'update post',
        component: () => import(/* webpackChunkName: "about" */ '../views/UpdateView.vue'),
        meta:{
          requiresAuth:true
        } 
        }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to, from, next)=>{
  if(to.matched.some(record=>record.meta.requiresAuth)){
  if(!createStore.getters.isLoggedIn){
    next('/login');
  }else{
    next();
  }
} else  if(to.matched.some(record=>record.meta.requiresGuest)){
  if(createStore.getters.isLoggedIn){
    next('/profile');
  }else{
    next();
  }
} else {
  next();
}
});
export default router
