<script>
import { defineComponent } from 'vue'
import { mapGetters, mapActions } from 'vuex';


// Components
export default defineComponent({
  name: 'HomeView',
  components:{
      
  },
    data:()=>({
      page:1,
      display:3,
      post:[] 
        
 }),
   computed:{
        ...mapGetters(['posts']),
        ...mapGetters(['isLoggedIn']),     
    },
 methods : {
     ...mapActions(['getPosts']),

      
     
 },
  created(){
    this.getPosts().then( this.posts.sort((a,b)=>{
      if(a.date < b.date){
        return 1;
      }
      if(a.date > b.date){
        return -1;
      }
      return 0;
    }))
    this.post =  this.posts
   
  
    
 },
  

});
</script>

